  background: linear-gradient(90deg, #df000f 0%, #df000f 100%);
  cursor: pointer;

  position: relative;

  &:after {
    content: url('data:image/svg+xml,<svg width="11" height="7" viewBox="0 0 11 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M5.5 4.53955L9.75188 -5.45572e-08L11 1.12789L5.5 7L-4.93014e-08 1.12788L1.24812 -4.26268e-07L5.5 4.53955Z" fill="white"/></svg>');
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.3s ease;
  }

  .closeicon {
    display: none;
  }
  img {
    width: 36px;
  }
  span {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #ffffff;
    padding-left: 7px;
  }
  .icon--blue {
    display: none;
  }
  &.active {
    background: #ca000e;
    /* .closeicon {
						display: block;
			 }
				.burgericon {
						display: none;
			 } */
  }
  &.active:after {
    transform: translateY(-50%) rotate(180deg);
  }

  &:hover {
    opacity: 1;
    background-color: #ca000e;
  }
  &:focus {
    opacity: 1;
    background-color: #ca000e;
  }

  &.disabled,
  &:disabled {
    opacity: 1;

    background-color: rgba(4, 163, 233, 0.2);
    span {
      color: #df000f;
      opacity: 0.5;
    }
    .icon--blue {
      display: initial;
    }
    .icon--white {
      display: none;
    }
  }
